.unlocked-container {
    background: linear-gradient(135deg, #6a11cb, #2575fc);
    padding: 1.75rem;
    border-radius: 1rem;
    box-shadow: 0 0 20px rgba(80, 120, 255, 0.5);
    color: white;
    animation: popIn 0.6s ease-out;
    max-width: 102%;
    text-align: center;
    position: relative;
}

.unlocked-close {
    position: absolute;
    right: 1.75rem;
}

.unlocked-title {
    font-size: 1.8rem;
    margin-bottom: 15px;
    font-weight: 700;
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.unlocked-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.unlocked-item {
    background: rgba(255, 255, 255, 0.15);
    padding: 12px;
    margin-bottom: 10px;
    border-radius: 12px;
    font-size: 1.1rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 10px;
    backdrop-filter: blur(6px);
    transition: transform 0.2s, box-shadow 0.2s;
}

.unlocked-item:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}

.item-badge {
    font-size: 1.4rem;
    filter: drop-shadow(0 0 6px gold);
}

@keyframes popIn {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}